import { Switch, Route } from "react-router-dom";

import Home from "./pages/Home";
import Dashboard from "./pages/Dashboard";

const Routes = () => {
  return (
    <Switch>
      <Route path="rippo-frotend/" exact>
        <Home />
      </Route>
      <Route path="rippo-frotend/dashboard">
        <Dashboard />
      </Route>
    </Switch>
  );
};

export default Routes;
